	adoptPrioritizeAdoptGuides: false,
	allowCrossOriginFrames: false,
	disableCookies: false,
	disableDesignerKeyboardShortcut: false,
	disableFeedbackAutoInit: false,
	disableGlobalCSS: false,
	disablePersistence: false,
